import React, { useState, useEffect } from "react";
import { makeStyles, withStyles } from "@material-ui/core/styles";
import MenuItem from "@material-ui/core/MenuItem";
import ListSubheader from "@material-ui/core/ListSubheader";
import FormControl from "@material-ui/core/FormControl";
import ListItemText from "@material-ui/core/ListItemText";
import CancelIcon from "@material-ui/icons/Cancel";
import Select from "@material-ui/core/Select";
import Checkbox from "@material-ui/core/Checkbox";
import Chip from "@material-ui/core/Chip";
import _without from "lodash-es/without";
import { useTranslation } from "react-i18next";
import KeyboardArrowDownIcon from "@material-ui/icons/KeyboardArrowDown";
import AddColumnIcon from "../../Icons/AddColumnIcon";
import styles from "../../components/LeftNavigation/DesignerControls/BasicControls.module.css";
import Tooltip from "../Tooltip/Tooltip";

const useStyles = makeStyles((styleProp) => ({
  formControl: {
    minWidth: (styleProp) => styleProp.minWidth - 35 || 120,
    maxWidth: (styleProp) => styleProp.maxWidth - 35 || 300,
    width: (styleProp) => styleProp.width || "100%"
  },
  list: {
    backgroundColor: "green"
  },
  input: {
    backgroundColor: "blue",
    padding: "4px"
  },
  addNewInput: {
    marginLeft: "28.25px"
  },
  focused: {
    backgroundColor: "red",
    border: 0
  },
  select: {
    backgroundColor: "red"
  },
  outlined: {
    padding: 0,
    paddingRight: 35,

    "&$focused": {
      border: 0
    }
  },
  iconOpen: {
  },
  root: {
    minWidth: (styleProp) => styleProp.minWidth - 35 || 120,
    maxWidth: (styleProp) => styleProp.maxWidth - 35 || 300,
    border: (styleProp) => styleProp.border || "1px solid #C4C4C4 !important",
    borderRadius: (styleProp) => styleProp.borderRadius || 2,
    textAlign: (styleProp) => styleProp.textAlign || "start",
    minHeight: (styleProp) => styleProp.maxHeight || 26,
    background: "#FFFFFF 0% 0% no-repeat padding-box !important",
    opacity: 1,
    height: "auto !important",

    "& .MuiOutlinedInput-notchedOutline": {
      borderColor: "green"
    },
    "&.Mui-focused .MuiOutlinedInput-notchedOutline": {
      borderColor: "red"
    },
    "& .MuiOutlinedInput-root.Mui-focused .MuiOutlinedInput-notchedOutline": {
      border: 0
    },
    "& .MuiOutlinedInput-root": {
      "& fieldset": {
        border: "1px solid #C4C4C4",
        borderRadius: "2px"
      },
      "&:hover fieldset": {
        border: "1px solid #C4C4C4",
        borderRadius: "2px"
      },
      "&.Mui-focused fieldset": {
        border: "1px solid #C4C4C4",
        borderRadius: "2px"
      },
      "& .MuiOutlinedInput-root.Mui-focused .MuiOutlinedInput-notchedOutline": {
        border: "1px solid #C4C4C4"
      }
    }
  },
  chips: {
    display: "flex",
    flexWrap: "wrap"
  },
  chipRoot: {
    margin: 4
  },
  chip: {
    margin: 4,
    height: "20px",
    textAlign: "left",
    font: "normal normal normal 12px/20px Open Sans",
    letterSpacing: 0,
    color: "#000000",
    opacity: 1,
    borderRadius: 2,
    "& label": {
      paddingLeft: -8,
      paddingRight: -8
    }
  },
  chipLabel: {
    paddingLeft: 4,
    paddingRight: 4
  },
  chipSizeSmall: {
    margin: 4
  },
  listSubheaderRoot: {
    textAlign: "left",
    font: "normal normal 600 11px/20px Open Sans",
    letterSpacing: "0px",
    color: "#000000",
    opacity: 1
  },

  notchedOutline: {
    border: 0
  }
}));

const CustomCheckbox = withStyles({
  root: {
    color: "#0072C6",
    "&$checked": {
      color: "#0072C6"
    }
  },
  icon: {
    width: 12,
    height: 12,
    color: "#0072C6",
    borderRadius: "2px",
    opacity: 1
  }
})((props) => <Checkbox color="default" {...props} />);

const CustomListSubheader = withStyles({
  root: {
    textAlign: "left",
    font: "normal normal 600 11px/20px Open Sans",
    letterSpacing: "0px",
    color: "#000000",
    opacity: 1,
    paddingLeft: "17px"
  }
})((props) => <ListSubheader {...props} />);

const CustomListItemText = withStyles({
  primary: {
    textAlign: "left",
    font: "normal normal normal 12px/20px Open Sans",
    letterSpacing: "0px",
    color: "#000000",
    opacity: 1
  },
  gutters: {
    paddingLeft: "27px"
  }
})((props) => <ListItemText {...props} />);

const CustomMenuItem = withStyles({
  root: {
    paddingTop: "0px",
    paddingBottom: "0px"
  },
  gutters: {
    paddingLeft: "17px"
  },
  selected: {
    backgroundColor: "rgba(0, 0, 0, 0.00) !important"
  }
})((props) => <MenuItem {...props} />);

export default function MultiSelectWithGrouping(props) {
  const categories = props.categories;

  const classes = useStyles({
    minWidth: props.minWidth,
    maxWidth: props.maxWidth,
    border: props.border
  });

  const { t } = useTranslation(["styles", "translation"]);

  const [selectedOptions, setSelectedOptions] = useState(props.initialSelected || []);
  const [dynamicSupportedDocOptions, setDynamicSupportedDocOptions] = useState([]);
  const [dynamicAllowedFilesOptions, setDynamicAllowedFilesOptions] = useState([]);
  const [newAddedOptions, setNewAddedOptions] = useState([]);
  const [showInput, setShowInput] = useState(false);

  function addNew(value) {
    const newOption = (
      <CustomMenuItem key={value} value={value} onKeyDown={(e) => e.stopPropagation()}>
        <CustomCheckbox checked={selectedOptions.includes(value)} color="blue" />
        <CustomListItemText primary={value} onKeyDown={(e) => e.stopPropagation()}/>
      </CustomMenuItem>
    );

    setDynamicSupportedDocOptions((prev) => [...prev, newOption]);
  }

  function handleInput(e) {
    e.stopPropagation();
    const { value = "" } = e.target;

    if (e.keyCode === 13 && value.trim()) {
      const newOption = { Title: value, value: value };
      const allOptions = [...newAddedOptions, newOption];

      setNewAddedOptions(allOptions);
      addNew(value);
      props.onChangeHandler(allOptions, "new");
      setShowInput(false);
    }
  }

  const setOptions = () => {
    const updatedDynamicSupportedDocOptions = [];
    const updatedDynamicAllowedFilesOptions = [];

    Object.keys(categories).forEach(function categoryFunc(categoryName, index) {
      if (categoryName !== "DOCS") {
        updatedDynamicAllowedFilesOptions.push(
          <CustomListSubheader key={categoryName} className={{ root: classes.listSubheaderRoot }} disabled>
            {categoryName}
          </CustomListSubheader>
        );
      }

      const newlyAddedOptions =
        categoryName === "DOCS"
          ? props?.selectedControl?.json?.available_doc_types?.[props?.docIndex]?.["SupportedDocumentsinDocType_new"] ||
            []
          : [];

      [...categories[categoryName], ...newlyAddedOptions].forEach((optionName) => {
        const newOption = (
          <CustomMenuItem key={optionName.value} value={optionName.value} onKeyDown={(e) => e.stopPropagation()}>
            <CustomCheckbox checked={selectedOptions.includes(optionName.value)} color="blue" />
            <CustomListItemText primary={optionName.value} onKeyDown={(e) => e.stopPropagation()}/>
          </CustomMenuItem>
        );

        if (categoryName !== "DOCS") {
          updatedDynamicAllowedFilesOptions.push(newOption);
        } else {
          updatedDynamicSupportedDocOptions.push(newOption);
        }
      });
    });

    setDynamicAllowedFilesOptions(updatedDynamicAllowedFilesOptions);
    setDynamicSupportedDocOptions(updatedDynamicSupportedDocOptions);
  };

  const handleChange = (event) => {
    const { value } = event.target || [];

    if (value) {
      setSelectedOptions(() => value.filter((element) => element !== undefined));
    }
  };

  const handleDelete = (e, value) => {
    e.preventDefault();
    setSelectedOptions((current) => _without(current, value));
  };

  useEffect(() => {
    setOptions();
    if (props.onChangeHandler) props.onChangeHandler(selectedOptions);

    return () => {
    };
  }, [selectedOptions]);

  const downArrow = (props) => <KeyboardArrowDownIcon {...props} style={{ top: "unset" }} />;

  return (
    <div>
      {Object.keys(props.categories)[0] === "DOCS" ? (
        <div className={classes.redBackground}>
          <FormControl className={classes.formControl} variant="outlined">
            <Select
              labelId={props.id}
              id={props.id}
              multiple
              value={selectedOptions}
              onChange={handleChange}
              IconComponent={downArrow}
              renderValue={(selected) => (
                <div className={classes.chips}>
                  {selected.map((value) => (
                    <Chip
                      size="small"
                      key={value}
                      className={classes.chip}
                      label={value}
                      deleteIcon={
                        props.deleteIcon ? (
                          <props.deleteIcon onMouseDown={(event) => event.stopPropagation()} />
                        ) : (
                          <CancelIcon onMouseDown={(event) => event.stopPropagation()} />
                        )
                      }
                      onDelete={(e) => handleDelete(e, value)}
                    />
                  ))}
                </div>
              )}
              classes={{
                root: classes.root,
                outlined: classes.outlined,
                focused: classes.focused,
                input: classes.input,
                list: classes.list,
                notchedOutline: classes.notchedOutline
              }}
            >
              {dynamicSupportedDocOptions}

              {showInput && (
                <div className={classes.addNewInput}>
                  <input aria-label={"New Option"} type="text" onKeyDown={handleInput} />
                  <Tooltip tooltip={t("translation:Press hit enter key to add new option")} />
                </div>
              )}

              <div className={styles.addColumnButtonDiv} onClick={() => setShowInput(true)}>
                <span className={styles.addColumnIconSpan}>
                  <AddColumnIcon />
                </span>
                {t("translation:Add New")}
              </div>
            </Select>
          </FormControl>
        </div>
      ) : (
        <div className={classes.redBackground}>
          <FormControl className={classes.formControl} variant="outlined">
            <Select
              labelId={props.id}
              id={props.id}
              multiple
              value={selectedOptions}
              onChange={handleChange}
              IconComponent={downArrow}
              renderValue={(selected) => (
                <div className={classes.chips}>
                  {selected.map((value) => (
                    <Chip
                      size="small"
                      key={value}
                      className={classes.chip}
                      label={value}
                      deleteIcon={
                        props.deleteIcon ? (
                          <props.deleteIcon onMouseDown={(event) => event.stopPropagation()} />
                        ) : (
                          <CancelIcon onMouseDown={(event) => event.stopPropagation()} />
                        )
                      }
                      onDelete={(e) => handleDelete(e, value)}
                    />
                  ))}
                </div>
              )}
              classes={{
                root: classes.root,
                outlined: classes.outlined,
                focused: classes.focused,
                input: classes.input,
                list: classes.list,
                notchedOutline: classes.notchedOutline
              }}
            >
              {dynamicAllowedFilesOptions}
            </Select>
          </FormControl>
        </div>
      )}
    </div>
  );
}
