import React, { useState, useEffect } from "react";
import { Card } from "react-bootstrap";
import ControlsMap from "../../../Properties/PropertiesConfiguration";
import DoclistPropertiesJson from "../../../Properties/DoclistPropertiesJson";
import CollapsibleComponent from "../../../../ReusableComponents/CollapseCard/CollapseCard";
import styles from "../../../LeftNavigation/DesignerControls/BasicControls.module.css";
import ToggleBtn from "../../../../ReusableComponents/ToggleBtn/ToggleBtn";
import AddColumnIcon from "../../../../Icons/AddColumnIcon";
import ControlLabel from "../../../../ReusableComponents/ControlLabel/ControlLabel";
import { ThemeJSON } from "../../Theme/ThemeJSON";
import PrimaryButton1 from "../../../../Icons/PrimaryButton1";
import SecondaryButton from "../../../../Icons/SecondaryButton";
import axios from "../../../../axios";
import TextButton from "../../../../Icons/TextButton";
import { regexValidator } from "../../../../Resources/globalFunctions";
import CustomizedAlert from "../../../../ReusableComponents/Alert/CustomizedAlert";
import { useTranslation } from "react-i18next";
import ErrorMessageComponent from "../../../../ReusableComponents/ErrorMessageComponent/ErrorMessageComponent";
import ImageUploadBtn from "../../../../ReusableComponents/ImageUploadBtn/ImageUploadBtn";
import ColorPicker from "../../../../ReusableComponents/ColorPicker/ColorPicker";
import MultiSelectWithGrouping from "../../../../ReusableComponents/MultiSelectWithGrouping/MultiSelectWithGrouping";
import { EventListjson } from "../../../../ReusableComponents/Events/EventList";
import Events from "../../../../ReusableComponents/Events/Events";
import { useDispatch } from "../../../../Redux";
import { ButtonGroup, Button } from "@material-ui/core";
import MappingFile from "../../../../ReusableComponents/Mapping/MappingFile";
import Dots from "../../../../Icons/ThreeDots.jsx";
import Doclist_parameter_mapping from "./Doclist_parameter_mapping.jsx";
import { fetchProcessModularData } from "../../../../components/IFormDesignerLayout/RequestLogic";
import CancelOutlinedIcon from "@material-ui/icons/CancelOutlined";
import { getFormId, removeMappingFromControl } from "../../IFormDesignerLogic";
import Tooltip from "../../../../ReusableComponents/Tooltip/Tooltip";
import cloneDeep from "lodash-es/cloneDeep";
import { GetStateFromData } from "../../../CustomHooks/CustomHooks.js";
import { updateFormStateContextWithoutRender, updateStateInRedux } from "../../../../Redux/Actions/FormStateActions.js";

const DoclistProperties = (props) => {
  const contexts = GetStateFromData(props.containerId);
  const dispatch = useDispatch();
  const {
    updateJson,
    dataObjectsContent,
    setDataObjectsContent,
    setfilteredDataObject,
    deleteControl,
    microFrontendData,
    fromQuickProps,
    setfilteredDataObjectCopy,
    filteredDataObjectCopy,
    setCurrentMappedDO,
    childComp,
    activePage,
    activeSubForm,
    imageBaseURL,
    builderMode,
    updateDataObjectAfterCreate,
    
  } = contexts.contexts;

  const { updatedComponentInFormState } = updateJson;

  const { t } = useTranslation(["styles", "translation"]);

  const [errorMessageId, setErrorMessageId] = useState(false);
  const [alertData, setAlertData] = useState({
    open: false,
    showConfirm: "showNotification"
});
  const [dimension, setDimension] = useState("min");
  const [mappingField, setMappingField] = useState(false);
  const [openDoclist_parameter_mapping, setOpenDoclist_parameter_mapping] = useState(false);
  const [associatedField, setAssociatedField] = useState([]);
  const translationStyles1 = window?.depsatkur_configs?.selectedLanguage === "ar" || window?.depsatkur_configs?.selectedLanguage === "ar-SA";
  const [defaultMappingDOOption, setDefaultMappingDOOption] = useState([]);

  useEffect(() => {
    if (fromQuickProps) setMappingField(true);
  }, [fromQuickProps]);

  function handleCombo(comboId, docIndex) {
    const select = document.getElementById(comboId);

    const propertyName = select.getAttribute("name");
    const optionTag = select.options[select.selectedIndex];

    if (propertyName === "DocumentListLabelFontWeightStyle" || propertyName === "DocumentTypeLabelFontWeightStyle") {
      let styleKey = "";
      let weightKey = "";
      if (propertyName === "DocumentListLabelFontWeightStyle") {
        weightKey = "DocumentListLabelFontWeight";
        styleKey = "DocumentListLabelFontStyle";
      } else if (propertyName === "DocumentTypeLabelFontWeightStyle") {
        weightKey = "DocumentTypeLabelFontWeight";
        styleKey = "DocumentTypeLabelFontStyle";
      }

      if (optionTag.value === "Bold Italic") {
        handleProperty(weightKey, "Bold", docIndex);
        handleProperty(styleKey, "Italic", docIndex);
      } else if (optionTag.value === "Bold") {
        handleProperty(weightKey, optionTag.value, docIndex);
        handleProperty(styleKey, "Normal", docIndex);
      } else if (optionTag.value === "Italic" || optionTag.value === "Normal") {
        handleProperty(weightKey, "Normal", docIndex);
        handleProperty(styleKey, optionTag.value, docIndex);
      } else if (optionTag.value === "Select") {
        handleProperty(weightKey, "Select", docIndex, true);
        handleProperty(styleKey, "Select", docIndex, true);
      }
    }

    if (propertyName === "AllowAutoCreationDO") {
      if (optionTag.value) {
        const formId = getFormId();

        const jsonToSend = {
          formId: formId.toString(),
          applicationId: microFrontendData?.applicationId,
          tableName: optionTag.value,
          controlId: props.selectedControl.id
        };

        axios
          .post("/idesign/createDoclist", jsonToSend)
          .then((res) => {
            if (res.data.status === "0") {
              const newObjectName = `${jsonToSend.tableName}.${res.data.doclistname}`;

              props.selectedControl.dataclass = newObjectName; // got from backend
              props.selectedControl.datamapid = res.data.doclistId; // got from backend
              props.selectedControl.datamapVariableType = "0";
              props.selectedControl.datamapPercision = "0";
              props.selectedControl.datamapVariableLength = "0";
              props.selectedControl.unBounded = "Y";
              props.selectedControl.dataObjectType = "M";

              props.selectedControl.style.Mapping = newObjectName; // got from backend
              props.selectedControl.style.MappingId = newObjectName; // got from backend
              props.selectedControl.style[propertyName] = optionTag.value; // got from backend

              dispatch(updateStateInRedux({name: "doclistRerenderCount", value: new Date(),key: props.containerId}));
              updateDataObjectAfterCreate(dataObjectsContent,optionTag.value,res.data.dataObject)
              setDataObjectsContent(dataObjectsContent);
              setfilteredDataObject(dataObjectsContent);
              setfilteredDataObjectCopy(dataObjectsContent);
              updatedComponentInFormState(props.selectedControl.id, props.selectedControl);

            } else {
              setAlertData({
                open: true,
                showConfirm: "showNotification",
                alertType: "error",
                alertContentMsg: "Failed to create default data object."
              });
            }
          })
          .catch(() => {
            setAlertData({
              open: true,
              showConfirm: "showNotification",
              alertType: "error",
              alertContentMsg: "Failed to create default data object."
            });
          });
      } else {
        props.selectedControl.dataclass = undefined; // got from backend
        props.selectedControl.datamapid = undefined; // got from backend
        props.selectedControl.datamapVariableType = undefined;
        props.selectedControl.datamapPercision = undefined;
        props.selectedControl.datamapVariableLength = undefined;
        props.selectedControl.unBounded = undefined;
        props.selectedControl.dataObjectType = undefined;

        props.selectedControl.style.Mapping = undefined; // got from backend
        props.selectedControl.style.MappingId = undefined; // got from backend
        props.selectedControl.style[propertyName] = optionTag.value; // got from backend

        dispatch(updateFormStateContextWithoutRender({name: "doclistRerenderCount", value: new Date(), key: props.containerId}));
        updatedComponentInFormState(props.selectedControl.id, props.selectedControl);
      }
    } else {
      handleProperty(propertyName, optionTag.value, docIndex, true);
    }
  }

  const updateEventData = (eventJson) => {
    const count = Object.keys(eventJson).length;
    if (count > 0) {
      const Events = {};
      Events["Events"] = eventJson;
      props.selectedControl["event"] = Events;
    }

    dispatch(updateStateInRedux({name: "doclistRerenderCount", value: new Date(),key: props.containerId}));
    updatedComponentInFormState(props.selectedControl.id, props.selectedControl);
  };

  function handleMultiSelect(propertyName, docIndex, selectedValues) {
    handleProperty(propertyName, selectedValues, docIndex);
  }

  function handleProperty(propertyName, propertyValue, docIndex, isCombo) {
    if (docIndex >= 0) {
      handleDocProperty(propertyName, propertyValue, docIndex);
    } else {
      try {

        props.selectedControl.style[propertyName] = propertyValue;

        if (propertyName === "DocumentLabel") props.selectedControl.style["Label"] = propertyValue;

        if (propertyName === "Enable") {
          props.selectedControl.style["previousEnable"] = propertyValue;
        }

        dispatch(updateStateInRedux({name: "doclistRerenderCount", value: new Date(),key: props.containerId}));
        updatedComponentInFormState(props.selectedControl.id, props.selectedControl);
        props.applyChanges(props.formData);
        setErrorMessageId(false);
      } catch (e) {
        console.log(e);
      }
    }
  }

  function validateRegex(propertyName, propertyValue, event, inputBoxId) {
    try {
      let passValue = event?.key;
      if (propertyName === "CustomId") {
        if(event.target.selectionEnd === event.target.selectionStart){
          passValue=[propertyValue.slice(0,event.target.selectionEnd)+event.key + propertyValue.slice(event.target.selectionEnd) ].join("");
        }
        else {
          let stringArray = propertyValue.split("");
          stringArray.splice(event.target.selectionStart, event.target.selectionEnd - event.target.selectionStart);
          stringArray.splice(event.target.selectionStart, 0, ...event?.key.split(""));
          passValue = stringArray.join("");
        }
      }
      if (
        event &&
        event.which !== 8 &&
        event.which !== 46 &&
        event.code !== "Tab" &&
        !regexValidator(passValue, ControlsMap[propertyName]["regexType"])
      ) {
        event.preventDefault();
        setAlertData({
          ...ControlsMap["customizedAlertNotificationData"],
          open: true,
          alertContentKey: "RegexErrorMessage"
        });
      } else {
        // setAlertData({
        //   showConfirm: "showNotification",
        //   open: false,          
        // });
        setErrorMessageId(false);
      }
    } catch (e) {
      console.log("Error while validating regex");
    }
  }

  function deleteDocument(e, docIndex) {
    let documentArr = props.selectedControl.documents;
    let availableDocumentArr = props.selectedControl.json["available_doc_types"];
    let newArr = documentArr.filter((document, index) => index !== docIndex);
    let newAvilableArr = availableDocumentArr.filter((document, index) => index !== docIndex);
    props.selectedControl.documents = [...newArr];
    props.selectedControl.json["available_doc_types"] = [...newAvilableArr];

    dispatch(updateStateInRedux({name: "doclistRerenderCount", value: new Date(),key: props.containerId}));

    updatedComponentInFormState(props.selectedControl.id, props.selectedControl);
    props.applyChanges(props.formData);
    e.stopPropagation();
  }

  if (DoclistPropertiesJson && ControlsMap) {
    var CollapsibleItems = [];
    Object.keys(DoclistPropertiesJson).forEach((key, index) => {
      let collapseComponentBody = [];
      Object.keys(DoclistPropertiesJson[key]).forEach((property) => {
        if (ControlsMap?.[property]?.type) {
          if (props?.selectedControl?.documents?.length > 0 && property === "DocumentTypeHeader") {
            props.selectedControl.documents.map((docDetails, index) => {
              collapseComponentBody.push(
                <CollapsibleComponent
                  id={index + ""}
                  heading={docDetails.DocumentName}
                  body={prepareHtmlTag(property, ControlsMap[property].type, docDetails, index)}
                  headerStyling={styles.documentTypeDivHeader}
                  divStyling={styles.documentTypeDivWrapper}
                  isDoclist={true}
                  docIndex={index}
                  deleteDocument={deleteDocument}
                />
              );
            });
          } else {
            if (property !== "DocumentTypeHeader") {
              collapseComponentBody.push(
                prepareHtmlTag(property, ControlsMap[property].type, props.selectedControl.style, -1)
              );
            }
          }
        }
      });

      CollapsibleItems.push(<CollapsibleComponent id={index + ""} heading={key} body={collapseComponentBody} />);
    });
  }

  function handleIcon(imageName, imageFile, docIndex, key) {
    if (docIndex >= 0) {
      handleDocProperty(key, imageFile, docIndex);
      if (key === "DocTypeIcon") {
        handleDocProperty("DocTypeIconName", imageName, docIndex);
      }
    }
  }

  function handleToggle(e, field, docIndex) {
    const value = e.target.value;

    if (docIndex >= 0) {
      handleDocProperty(field, value, docIndex);
    } else {
      handleProperty(field, value);
    }
  }

  function mappingOpen() {
    setMappingField(true);
  }

  function handleDocProperty(propertyKey, propertyValue, docIndex) {
    const json = props.selectedControl.json;

    if (docIndex >= 0 && props.selectedControl?.documents && json) {
      const lastValue = props.selectedControl.documents[docIndex][propertyKey];
      let newValue = propertyValue;

      if (propertyKey === "DocumentName") {
        if (
          props.selectedControl?.documents.some((doc) => doc.DocumentName === (propertyValue || "").trim()) &&
          lastValue !== propertyValue
        ) {
          setAlertData({
            open: true,
            showConfirm: "showNotification",
            alertType: "error",
            alertContentMsg: "Document name already exist."
          });
          newValue = lastValue;
        }
        if ((propertyValue || "").trim() === "") {
          setAlertData({
            open: true,
            showConfirm: "showNotification",
            alertType: "error",
            alertContentMsg: "Document label name is required"
          });
          newValue = lastValue;
        }
        json.available_doc_types[docIndex]["name"] = newValue;
      }
      if (propertyKey === "Mandatory") {
        json.available_doc_types[docIndex]["mandatory"] = propertyValue === "true";
      }
      if (propertyKey === "AllowMultipleUploads") {
        json.available_doc_types[docIndex]["multiple_upload"] = propertyValue === "true";
      }
      if (propertyKey === "AllowedFileTypes") {
        json.available_doc_types[docIndex]["allowed_file_types"] = propertyValue;
      }
      if (propertyKey === "DocTypeIcon") {
        const isPortal = builderMode === "app" || builderMode === "application";
        json.available_doc_types[docIndex]["doc_type_icon"] = isPortal ? `${imageBaseURL}${propertyValue}` : propertyValue;
      }
      if (propertyKey === "DocTypeIconName") {
        json.available_doc_types[docIndex]["doc_type_icon_name"] = propertyValue;
      }
      if (propertyKey === "DimensionsOfImage_height") {
        if (dimension === "min") {
          json.available_doc_types[docIndex]["min_image_height"] = propertyValue;
        } else if (dimension === "max") {
          json.available_doc_types[docIndex]["max_image_height"] = propertyValue;
        }
      }
      if (propertyKey === "DimensionsOfImage_width") {
        if (dimension === "min") {
          json.available_doc_types[docIndex]["min_image_width"] = propertyValue;
        } else if (dimension === "max") {
          json.available_doc_types[docIndex]["max_image_width"] = propertyValue;
        }
      }
      if (propertyKey === "SupportedDocumentsinDocType") {
        json.available_doc_types[docIndex]["description"] = propertyValue.join();
      }
      if (propertyKey === "SupportedDocumentsinDocType_new") {
        json.available_doc_types[docIndex]["SupportedDocumentsinDocType_new"] = propertyValue;
      }
      if (propertyKey === "MaxSizeOfDoc") {
        if (props.selectedControl.documents[docIndex]["MaxSizeOfDocType"] === "KB") {
          propertyValue *= 1024; // converting KB to Bytes
        } else {
          propertyValue *= 1024 * 1024; // converting MB to Bytes
        }
        json.available_doc_types[docIndex]["max_file_size"] = propertyValue;
      }
      if (propertyKey === "MaxSizeOfDocType") {
        json.available_doc_types[docIndex]["MaxSizeOfDocType"] = propertyValue;
      }
      if (propertyKey === "UploadOption") {
        const doc_upload_options = {
          google_drive: false,
          camera: false,
          scan: false,
          omnidocs: false,
          system: false
        };
        if (propertyValue === "1") {
          doc_upload_options.system = true;
        } else if (propertyValue === "2") {
          doc_upload_options.camera = true;
        } else if (propertyValue === "3") {
          doc_upload_options.system = true;
          doc_upload_options.camera = true;
        }
        json.available_doc_types[docIndex]["doc_upload_options"] = doc_upload_options;
      }
      if(propertyKey === "allow_comment") {
        json.available_doc_types[docIndex]["allow_comment"] = propertyValue === "true" || propertyValue === true;
      }
      if(propertyKey === "mandatory_comments") {
        json.available_doc_types[docIndex]["mandatory_comments"] = propertyValue === "true" || propertyValue === true;
      }

      if (propertyKey === "DocumentName") {
        props.selectedControl.documents[docIndex][propertyKey] = newValue;
      } else {
        props.selectedControl.documents[docIndex][propertyKey] = propertyValue;
      }
      props.applyChanges(props.formData);
    }

    dispatch(updateStateInRedux({name: "doclistRerenderCount", value: new Date(),key: props.containerId}));
    updatedComponentInFormState(props.selectedControl.id, props.selectedControl);
  }

  const getNewUniqueId = (arr = [{}], key = "") => {
    const newCopyArr = cloneDeep(arr || null);
    const sortedArr = newCopyArr.sort((a, b) => parseFloat(b[key]) - parseFloat(a[key]));
    return parseFloat(sortedArr[0][key]) + 1 + "";
  };

  function addDocument() {
    if (props.selectedControl?.documents && props.selectedControl?.documents.length) {
      const newId = getNewUniqueId(props.selectedControl.json.available_doc_types, "id");

      props.selectedControl["documents"].push(
        Object.assign(
          {},
          {
            ...ThemeJSON.documentType,
            DocumentName: `Document ${newId}`,
            UploadOption: props?.selectedControl?.style?.UploadOption
          }
        )
      );

      let new_available_doc_types = {
        id: newId,
        name: `Document ${newId}`,
        description: "",
        mandatory: false,
        allowed_file_types: [],
        max_file_size: 5000000,
        multiple_upload: true
      };

      props.selectedControl.json["available_doc_types"].push(new_available_doc_types);
      dispatch(updateStateInRedux({name: "doclistRerenderCount", value: new Date(),key: props.containerId}));
    } else if (props.selectedControl) {
      props.selectedControl["documents"] = [];

      props.selectedControl["documents"].push(
        Object.assign({}, { ...ThemeJSON.documentType, UploadOption: props?.selectedControl?.style?.UploadOption })
      );

      let new_available_doc_types = {
        id: `1`,
        name: `Document 1`,
        description: "",
        mandatory: false,
        allowed_file_types: [],
        max_file_size: 5000000,
        multiple_upload: true
      };

      props.selectedControl.json["available_doc_types"].push(new_available_doc_types);
      dispatch(updateStateInRedux({name: "doclistRerenderCount", value: new Date(),key: props.containerId}));
    }

    props.applyChanges(props.formData);
    updatedComponentInFormState(props.selectedControl.id, props.selectedControl);
  }

  function handle_openDoclist_parameter_mapping() {
    setOpenDoclist_parameter_mapping(true);
  }

  function saveParamter(parameter_Json) {
    props.selectedControl["mappingJson"] = parameter_Json;

    dispatch(updateStateInRedux({name: "doclistRerenderCount", value: new Date(),key: props.containerId}));

    updatedComponentInFormState(props.selectedControl.id, props.selectedControl);
    setOpenDoclist_parameter_mapping(false);
  }

  useEffect(() => {
    if (dataObjectsContent && dataObjectsContent.length > 0) {
      const topParentDO = dataObjectsContent[0];
      if (topParentDO.children && topParentDO.children.length > 0) {
        const secondLevelDO = topParentDO.children.filter((item) => item?.children?.length >= 0);
        setDefaultMappingDOOption(secondLevelDO);
      }
    }
  }, [dataObjectsContent]);

  useEffect(() => {
    if (dataObjectsContent && Object.keys(dataObjectsContent).length === 0) {
      const jsonForProcess = {
        formId: microFrontendData?.formDefId,
        processdefId: microFrontendData?.processId,
        processState: microFrontendData?.statusType,
        processName: microFrontendData?.processName
      };

      fetchProcessModularData(jsonForProcess)
        .then((response) => {
          setAssociatedField(response.data["children"]);
          setDataObjectsContent(response.data["children"]);
          setfilteredDataObject(response.data["children"]);
          setfilteredDataObjectCopy(response.data["children"]);
        })
        .catch(function (error) {
          console.log("Error :: ", error);
        });
    } else if (dataObjectsContent && Object.keys(dataObjectsContent).length > 0) {
      setAssociatedField(dataObjectsContent);
    }
  }, []);

  function prepareHtmlTag(key, type, styleData, docIndex) {
    const PropertiesLabel = ({ mandatory, button }) => (
      <span
        className={styles.propertiesLabel + (button ? " addButtonHover" : "")}
        style={button && { color: "#0072C6" }}
      >
        {t(`translation:${ControlsMap[key].label}`) || `${ControlsMap[key].label}`}
        {mandatory ? <span className={styles.mandatory}> *</span> : null}
      </span>
    );

    const InputTextSelect = ({ id, defaultValue, propertyName }) => (
      <div style={{ display: "flex" }}>
        <input
          type="text"
          aria-label={"Control Value1"}
          defaultValue={defaultValue}
          className={[styles.RightLabelTextInputDoclist, errorMessageId === id ? styles.inputBoxErrorStyle : null].join(
            " "
          )}
          onKeyUp={(event) => validateRegex(key, event.target.value, event, id)}
          onBlur={(event) => handleProperty(propertyName, event.target.value, docIndex)}
        />

        <select
          className={[styles.propertiesSelectInput].join(" ")}
          name={key + "Type"}
          id={`${id}${docIndex}`}
          onChange={() => handleCombo(`${id}${docIndex}`, docIndex)}
        >
          {ControlsMap[key].children.map((optionTagData, optionCount) => (
            <option
              value={optionTagData}
              selected={
                key === "DimensionsOfImage"
                  ? true
                  : props?.selectedControl && styleData?.[key + "Type"] === ControlsMap[key].children[optionCount]
              }
            >
              {t(`translation:${optionTagData}`)}
            </option>
          ))}
        </select>
      </div>
    );

    switch (type.toLowerCase()) {
      case "combo":
        return (
          <div className={styles.parentDiv} style={{ display: "flex" }}>
            <PropertiesLabel />

            <div className={[key === "AllowedFileTypes" ? styles.doclistAlowedFileTypes : null].join(" ")}>
              <select
                className={[
                  styles.RightLabelSelect1Table,
                  key === "AllowedFileTypes" ? styles.doclistAlowedFileTypes : null
                ].join(" ")}
                name={key}
                id={props.selectedControl.id + key}
                onChange={() => handleCombo(props.selectedControl.id + key, docIndex)}
                key={props.selectedControl.id}
              >
                {ControlsMap[key].children.map((_, optionCount) => {
                  if (ControlsMap[key].children[optionCount].Value) {
                    return (
                      <option
                        style={ControlsMap[key].children[optionCount]?.style}
                        value={ControlsMap[key].children[optionCount].Value}
                        key={ControlsMap[key].children[optionCount].Value}
                        selected={
                          props?.selectedControl && styleData?.[key] === ControlsMap[key].children[optionCount].Value
                        }
                      >
                        {t(`translation:${ControlsMap[key].children[optionCount].Title}`)}                        
                      </option>
                    );
                  }
                })}
              </select>
            </div>
          </div>
        );

      case "multiselectgroup":
        return (
          <div className={styles.parentDiv}>
            <PropertiesLabel />

            <div
              className={[
                key === "AllowedFileTypes"
                  ? styles.doclistAlowedFileTypes
                  : key === "SupportedDocumentsinDocType"
                  ? styles.SupportedDocumentsinDocType
                  : null
              ].join(" ")}
            >
              <MultiSelectWithGrouping
                categories={ControlsMap[key].children}
                key={props.selectedControl.id}
                id={props.selectedControl.id + key}
                name={key}
                initialSelected={(props?.selectedControl && styleData?.[key]) || []}
                minWidth={233}
                maxWidth={267}
                deleteIcon={undefined}
                docIndex={docIndex}
                selectedControl={props.selectedControl}
                onChangeHandler={(selectedValues, type) =>
                  handleMultiSelect(type === "new" ? `${key}_new` : key, docIndex, selectedValues)
                }
              />
            </div>
          </div>
        );

      case "text_top_bottom":
        return (
          <>
            <PropertiesLabel mandatory={ControlsMap[key]?.label === "Document Type Label"} />

            <div>
              <input
                type="text"
                aria-label={"Control Value2"}
                defaultValue={
                  ControlsMap[key]?.label === "Document Type Label"
                    ? props.selectedControl.documents[docIndex].DocumentName
                    : props.selectedControl.style[key]
                }
                key={props.selectedControl.id}
                id={props.selectedControl.id + key}
                maxLength={window?.depsatkur_configs?.label_max_length}
                className={[
                  styles.fullWidthTextBox,
                  errorMessageId === props.selectedControl.id + key ? styles.inputBoxErrorStyle : null
                ].join(" ")}
                onKeyPress={(event) => validateRegex(key, event.target.value, event, props.selectedControl.id + key)}
                onKeyUp={(event) => validateRegex(key, event.target.value, event, props.selectedControl.id + key)}
                onBlur={(event) => handleProperty(key, event.target.value, docIndex)}
              />
            </div>

            {errorMessageId === props.selectedControl.id + key ? (
              <ErrorMessageComponent errorMessage="non-permissible value" />
            ) : null}
          </>
        );

      case "mapping":
        return (
          <div>
            <div style={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>
              <PropertiesLabel />

              {childComp === "application" || activePage || activeSubForm ? null : (
                <span style={{ color: "#0072c6", cursor: "pointer" }} onClick={handle_openDoclist_parameter_mapping}>
                  {t(`translation:${"VIEW_PARAMETER_MAPPING"}`)}
                </span>
              )}
            </div>

            <div style={{ display: "flex" }}>
              <input
                type="text"
                aria-label={"Control Value3"}
                disabled={true}
                defaultValue={props.selectedControl.style[key === "doclistMapping" ? "Mapping" : key]}
                key={props.selectedControl.id + props.selectedControl.style[key]}
                id={props.selectedControl.id + key}
                className={[
                  styles.fullWidthTextBox,
                  errorMessageId === props.selectedControl.id + key ? styles.inputBoxErrorStyle : null
                ].join(" ")}
                onKeyUp={(event) => validateRegex(key, event.target.value, event, props.selectedControl.id + key)}
                onBlur={(event) => handleProperty(key, event.target.value, event)}
              />

              {props?.selectedControl?.dataclass ? (
                <span
                  title={t("translation: Remove Mapping")}
                  onClick={() => {
                    removeMappingFromControl(
                      props.selectedControl,
                      updatedComponentInFormState,
                      filteredDataObjectCopy,
                      setCurrentMappedDO
                    );
                  }}
                  style={
                    !translationStyles1
                      ? { position: "absolute", right: "15%", paddingTop: "5px", paddingRight: "5px" }
                      : { position: "absolute", left: "15%", paddingTop: "5px", paddingLeft: "5px" }
                  }
                >
                  <CancelOutlinedIcon />
                </span>
              ) : null}

              <span
                title={t("translation: View Data Objects")}
                className={styles.mappingDotsIcon}
                style={translationStyles1 ? { borderLeft: "1px solid #C4C4C4" } : null}
                onClick={() => mappingOpen()}
              >
                <Dots />
              </span>
            </div>

            {openDoclist_parameter_mapping && (
              <Doclist_parameter_mapping
                containerId={props.containerId}
                handleOpen={setOpenDoclist_parameter_mapping}
                data={props.selectedControl}
                saveParamter={saveParamter}
                associatedFieldKeys={associatedField}
              />
            )}
          </div>
        );

      case "toggle":
        return (
          <div className={"toggleBtn"} style={{ alignItems: "center" }}>
            <PropertiesLabel />

            <div className={translationStyles1 ? styles.toggleCheckboxArabic : styles.toggleCheckbox}>
              <ToggleBtn
                toggleState={styleData?.[key]}
                handleToggle={(arg1, arg2) => handleToggle(arg1, arg2, docIndex)}
                field={key}
                key={props.selectedControl.id}
                id={props?.selectedControl?.style?.CustomId || props?.selectedControl?.id}
              />
            </div>
          </div>
        );

      case "toggleselect":
        return childComp === "application" || activePage || activeSubForm ? (
          <>
            <div className={styles.toggleBtn} style={{ alignItems: "center" }}>
              <PropertiesLabel />

              <div className={styles.toggleCheckbox}>
                <ToggleBtn
                  toggleState={styleData?.[key]}
                  handleToggle={(arg1, arg2) => handleToggle(arg1, arg2, docIndex)}
                  field={key}
                  key={props.selectedControl.id}
                  id={props?.selectedControl?.style?.CustomId || props?.selectedControl?.id}
                />
              </div>
            </div>

            {props?.selectedControl?.style?.AllowAutoCreation === "true" ? (
              <div className={styles.parentDiv} style={{ display: "flex", gap: "10px" }}>
                <Tooltip tooltip={t(`translation:${ControlsMap[key].label2}`)} />

                <select
                  className={styles.RightLabelSelect1Table}
                  name={`${key}DO`}
                  id={props.selectedControl.id + key}
                  onChange={() => handleCombo(props.selectedControl.id + key, docIndex)}
                  key={props.selectedControl.id}
                  style={{ width: "100%" }}
                >
                  <option
                    value={""}
                    selected={props?.selectedControl?.style?.[`${key}DO`] === ""}
                  >
                    Select
                  </option>

                  {defaultMappingDOOption?.length >= 0 &&
                    defaultMappingDOOption.map((item) => (
                      <option
                        value={item.VariableName}
                        key={item.VariableName}
                        selected={props?.selectedControl?.style?.[`${key}DO`] === item.VariableName}
                      >
                        {t(`translation:${item.VariableName}`)}
                      </option>
                    ))}
                </select>
              </div>
            ) : null}
          </>
        ) : null;

      case "buttongroup":
        return (
          <div className={styles.parentDiv}>
            <PropertiesLabel mandatory />

            {dimension === "min" ? (
              <ButtonGroup style={{ display: "flex" }}>
                <Button
                  disableFocusRipple
                  disableRipple
                  tabIndex={0}
                  className={`formBuilder-MuiButton-outlined`}
                  variant="contained"
                  color="primary"
                  onClick={() => setDimension("min")}
                  style={{ whiteSpace: "nowrap" }}
                  key={props.selectedControl.id}
                >
                  {t(`translation:Min Dimensions`)}
                </Button>

                <Button
                  disableFocusRipple
                  disableRipple
                  tabIndex={0}
                  className={`formBuilder-MuiButton-outlined`}
                  variant="text"
                  color="primary"
                  onClick={() => setDimension("max")}
                  style={{ whiteSpace: "nowrap" }}
                  key={props.selectedControl.id}
                >
                  {t(`translation:Max Dimensions`)}
                </Button>
              </ButtonGroup>
            ) : (
              <ButtonGroup style={{ display: "flex" }}>
                <Button
                  disableFocusRipple
                  disableRipple
                  tabIndex={0}
                  className={`formBuilder-MuiButton-outlined`}
                  variant="text"
                  color="primary"
                  onClick={() => setDimension("min")}
                  style={{ whiteSpace: "nowrap" }}
                  key={props.selectedControl.id}
                >
                  {t(`translation:Min Dimensions`)}
                </Button>

                <Button
                  disableFocusRipple
                  disableRipple
                  tabIndex={0}
                  className={`formBuilder-MuiButton-outlined`}
                  variant="contained"
                  color="primary"
                  onClick={() => setDimension("max")}
                  style={{ whiteSpace: "nowrap" }}
                  key={props.selectedControl.id}
                >
                  {t(`translation:Max Dimensions`)}
                </Button>
              </ButtonGroup>
            )}

            <div style={{ display: "flex", "justify-content": "space-between", "margin-top": "10px", gap: "12px" }}>
              <div>
                <InputTextSelect
                  aria-label={"Control Value4"}
                  id={`${props.selectedControl.id}${key}height`}
                  defaultValue={
                    dimension === "min"
                      ? props?.selectedControl?.json.available_doc_types?.[docIndex]?.min_image_height
                      : props?.selectedControl?.json?.available_doc_types?.[docIndex]?.max_image_height
                  }
                  propertyName={"DimensionsOfImage_height"}
                  key={props.selectedControl.id}
                />
              </div>

              <div>
                <InputTextSelect
                  aria-label={"Control Value5"}
                  id={`${props.selectedControl.id}${key}width`}
                  defaultValue={
                    dimension === "min"
                      ? props?.selectedControl?.json.available_doc_types?.[docIndex]?.min_image_width
                      : props?.selectedControl?.json.available_doc_types?.[docIndex]?.max_image_width
                  }
                  propertyName={"DimensionsOfImage_width"}
                  key={props.selectedControl.id}
                />
              </div>
            </div>

            {errorMessageId === `${props.selectedControl.id}${key}width` ? (
              <ErrorMessageComponent errorMessage="non-permissible value" />
            ) : null}
          </div>
        );

      case "text":
        return (
          <div className={styles.parentDiv}>
            <PropertiesLabel mandatory />

            <div>
              <InputTextSelect
                aria-label={"Control Value6"}
                id={`${props.selectedControl.id}${key}`}
                defaultValue={
                  props.selectedControl.documents[docIndex]["MaxSizeOfDocType"] === "KB"
                    ? props?.selectedControl?.json.available_doc_types?.[docIndex]?.max_file_size / 1024 // converting Bytes to KB
                    : props?.selectedControl?.json.available_doc_types?.[docIndex]?.max_file_size / (1024 * 1024)
                } // converting Bytes to MB
                propertyName={key}
                key={props.selectedControl.id}
              />
            </div>

            {errorMessageId === props.selectedControl.id + key ? (
              <ErrorMessageComponent errorMessage="non-permissible value" />
            ) : null}
          </div>
        );

      case "textbox":
        return (
          <div>
            <div className={styles.leftLabelForTextBox}>
              <PropertiesLabel />

              <input
                type="text"
                aria-label={"Control Value7"}
                defaultValue={props.selectedControl.style[key]}
                key={props.selectedControl.id}
                id={props.selectedControl.id + key}
                className={[
                  styles.textBoxLayoutForLable,
                  errorMessageId === props.selectedControl.id + key ? styles.inputBoxErrorStyle : null
                ].join(" ")}
                onKeyUp={(event) => validateRegex(key, event.target.value, event, props.selectedControl.id + key)}
                onBlur={(event) => handleProperty(key, event.target.value, event)}
              />
            </div>

            {errorMessageId === props.selectedControl.id + key ? (
              <ErrorMessageComponent errorMessage="non-permissible value" />
            ) : null}
          </div>
        );

      case "textarea":
        return (
          <div className={styles.parentDiv}>
            <PropertiesLabel />

            <textarea
              aria-label={props.selectedControl.style[key]}
              className={[
                styles.textBoxLayoutStyletextArea,
                styles.textareaNoTopMargin,
                errorMessageId === props.selectedControl.id + key ? styles.inputBoxErrorStyle : null
              ].join(" ")}
              onKeyUp={(event) => validateRegex(key, event.target.value, event, props.selectedControl.id + key)}
              onBlur={(event) => handleProperty(key, event.target.value, docIndex)}
              key={props.selectedControl.id}
              defaultValue={props.selectedControl.style[key]}
            />

            {errorMessageId === props.selectedControl.id + key ? (
              <ErrorMessageComponent errorMessage="non-permissible value" />
            ) : null}
          </div>
        );

      case "header":
        return (
          <p
            className={styles.propertiesHeader5}
            style={
              window?.depsatkur_configs?.selectedLanguage === "ar" || window?.depsatkur_configs?.selectedLanguage === "ar-SA"
                ? { display: "flex", justifyContent: "space-between" }
                : null
            }
          >
            {t(`translation:${ControlsMap[key].label}`)}
          </p>
        );

      case "hr":
        return (
          <hr
            id={key}
            // className={styles.hrTag}
            style={{ marginTop: "30px", marginLeft: "9px", marginRight: "9px", padding: "0 !important" }}
          ></hr>
        );

      case "select":
        return (
          <div className={styles.parentDiv}>
            <PropertiesLabel />

            <div>
              <select
                className={styles.documentsUploadModeSelect}
                name="selectionMethod"
                onChange={(e) => handleProperty(key, e.target.value, docIndex)}
                key={props.selectedControl.id}
              >
                {ControlsMap[key].children.map((child, index) => {
                  return (
                    <option
                      selected={styleData && styleData[key] === child.Value}
                      value={child.Value}
                      key={child.Value}
                    >
                      {t(`translation:${child.Title}`)}
                    </option>
                  );
                })}
              </select>
            </div>
          </div>
        );

      case "colorpicker":
        return (
          <div className={["colorpicker", styles.parentDiv2].join(" ")} style={{ margin: 0 }}>
            <PropertiesLabel />

            <ColorPicker
              color={styleData?.[key]}
              handleProperty={(arg1, arg2) => handleProperty(arg1, arg2, docIndex)}
              propertyName={key}
              key={props.selectedControl.id}
            />
          </div>
        );

      case "imageupload":
        return (
          <div className={styles.parentDiv}>
            <PropertiesLabel />

            <ImageUploadBtn
              containerId={props.containerId}
              handleImageUpload={(arg1, arg2) => handleIcon(arg1, arg2, docIndex, key)}
              iconName={styleData?.iconName || styleData.DocTypeIconName}
            />
          </div>
        );

      case "documenttype":
        return (
          <div className={styles.documentTypeDiv}>
            {Object.keys(ControlsMap[key].details).map((subKey, index) => {
              return prepareHtmlTag(subKey, ControlsMap[key].details[subKey]["type"], styleData, docIndex);
            })}
          </div>
        );

      case "fullwidthinputbutton":
        return (
          <div
            className={styles.addColumnButtonDiv + " qtwidebutton"}
            onClick={() => addDocument()}
            style={{ paddingBottom: "6px" }}
            key={props.selectedControl.id}
          >
            <span className={styles.addColumnIconSpan} style={{ marginBottom: "4px" }}>
              <AddColumnIcon />
            </span>

            <PropertiesLabel button />
          </div>
        );

      case "styleoptions":
        return (
          <div className={styles.parentDiv}>
            <div className={styles.doclistStyles}>
              <div className={styles.doclistStylesInnerDiv}>
                <img
                  src={require("../../../../Icons/Style1.svg").default}
                  alt="Loading..."
                  className={
                    styleData[ControlsMap[key].subKey] === "Style1"
                      ? [styles.selectedDoclistStyles, styles.doclistStylesInnerDivIcon].join(" ")
                      : styles.doclistStylesInnerDivIcon
                  }
                  onClick={() => handleProperty(ControlsMap[key].subKey, "Style1")}
                />
                {"Style1"}
              </div>

              <div className={styles.doclistStylesInnerDiv}>
                <img
                  src={require("../../../../Icons/Style2.svg").default}
                  alt="Loading..."
                  className={
                    styleData[ControlsMap[key].subKey] === "Style2"
                      ? [styles.selectedDoclistStyles, styles.doclistStylesInnerDivIcon].join(" ")
                      : styles.doclistStylesInnerDivIcon
                  }
                  onClick={() => handleProperty(ControlsMap[key].subKey, "Style2")}
                />
                {"Style2"}
              </div>
            </div>
          </div>
        );

      case "button":
        return (
          <div className={styles.parentDiv}>
            <div className={styles.doclistButtonTypeParentDiv}>
              <div
                className={styles.doclistButtonTypeDiv0}
                key={props.selectedControl.id}
                onClick={() => handleProperty(key, ControlsMap[key].children[0].Title)}
              >
                <div
                  className={[
                    styles.doclistButtonTypeDiv,
                    styleData[key] === ControlsMap[key].children[0].Title ? styles.doclistButtonTypeSelectedDiv : null
                  ].join(" ")}
                  style={
                    window?.depsatkur_configs?.selectedLanguage === "ar" || window?.depsatkur_configs?.selectedLanguage === "ar-SA" ? { direction: "ltr" } : null
                  }
                >
                  <PrimaryButton1 />
                </div>
                <div>{t(`translation:${ControlsMap[key].children[0].Title}`)}</div>
              </div>

              <div
                className={styles.doclistButtonTypeDiv0}
                key={props.selectedControl.id}
                onClick={() => handleProperty(key, ControlsMap[key].children[1].Title)}
              >
                <div
                  className={[
                    styles.doclistButtonTypeDiv,
                    styleData[key] === ControlsMap[key].children[1].Title ? styles.doclistButtonTypeSelectedDiv : null
                  ].join(" ")}
                  style={
                    window?.depsatkur_configs?.selectedLanguage === "ar" || window?.depsatkur_configs?.selectedLanguage === "ar-SA" ? { direction: "ltr" } : null
                  }
                >
                  <SecondaryButton />
                </div>

                <div>{t(`translation:${ControlsMap[key].children[1].Title}`)}</div>
              </div>

              <div
                className={styles.doclistButtonTypeDiv0}
                key={props.selectedControl.id}
                onClick={() => handleProperty(key, ControlsMap[key].children[2].Title)}
              >
                <div
                  className={[
                    styles.doclistButtonTypeDiv,
                    styleData[key] === ControlsMap[key].children[2].Title ? styles.doclistButtonTypeSelectedDiv : null
                  ].join(" ")}
                  style={
                    window?.depsatkur_configs?.selectedLanguage === "ar" || window?.depsatkur_configs?.selectedLanguage === "ar-SA" ? { direction: "ltr" } : null
                  }
                >
                  <TextButton />
                </div>

                <div>{t(`translation:${ControlsMap[key].children[2].Title}`)}</div>
              </div>
            </div>
          </div>
        );

      case "textareamandatorymessage":
        return (
          <>
            {props.selectedControl.style["Mandatory"] !== undefined &&
            props.selectedControl.style["Mandatory"] !== null &&
            props.selectedControl.style["Mandatory"] === "true" ? (
              <div>
                <div className={styles.errorMandatoryMessage}>{t(`translation:${"Error message if not filled"}`)}</div>

                <div className={styles.leftLabelForTextBox}>
                  <p>
                    {t(`translation:${ControlsMap[key].label}`)}
                  </p>

                  <textarea
                  aria-label={props.selectedControl.style[key]}
                    className={[
                      styles.textBoxLayoutStyletextAreaSpecialCharacter,
                      errorMessageId === props.selectedControl.id + key ? styles.inputBoxErrorStyle : null
                    ].join(" ")}
                    defaultValue={props.selectedControl.style[key]}
                    key={props.selectedControl.id}
                    id={props.selectedControl.id + key}
                    placeholder={"Enter Error Message"}
                    onKeyUp={(event) => validateRegex(key, event.target.value, event, props.selectedControl.id + key)}
                    onBlur={(event) => handleProperty(key, event.target.value, event)}
                  />
                </div>

                {errorMessageId === props.selectedControl.id + key ? (
                  <ErrorMessageComponent errorMessage="non-permissible value" />
                ) : null}
              </div>
            ) : null}
          </>
        );

      case "eventbutton":
        return (
          <Events
            key={props.selectedControl.id + "_eventsProperty"}
            json={EventListjson}
            value={props.selectedControl.event}
            elemtype={props.selectedControl.elem_type}
            updateEventData={(eventJson) => updateEventData(eventJson)}
          ></Events>
        );
    }
  }

  return (
    <>
      <ControlLabel
        label="Doclist Properties"
        handleDelete={deleteControl}
        id={props.selectedControl.id}
        customId={props?.selectedControl?.style?.CustomId}
      />

      <div className="propertiesBody">
        <Card.Body className={styles.cardBodyButtonProperties}>
          {CollapsibleItems.map((item, index) => {
            return item;
          })}
        </Card.Body>
      </div>
      {mappingField ? (
        <MappingFile
          stateOpen={true}
          setMappingField={setMappingField}
          controlData={props.selectedControl}
          elementType={props.selectedControl.elem_type}
        />
      ) : null}
      {
              alertData.open === true &&
            <CustomizedAlert alertData={alertData} />
            }
    </>
  );
};

export default DoclistProperties;
