import React, { useState, useEffect } from "react";
import styles from "./Doclist_parameter_mapping.module.css";
import { BlueButton, DullButton } from "../../../../ReusableComponents/GetButtons/GetButtons";
import { useTranslation } from "react-i18next";
import { GetStateFromData } from "../../../CustomHooks/CustomHooks";

function Doclist_parameter_mapping(props) {
  const contexts = GetStateFromData(props.containerId);

  const { dataObjectsContent } = contexts.contexts;

  const { t } = useTranslation(["styles", "translation"]);

  const parameter_arr_Default = [
    { label: "Doc Type", value: "DocType" },
    { label: "Doc Name", value: "DocName" },
    { label: "Doc Size", value: "DocSize" },
    { label: "Doc Comment", value: "DocComment" },
    { label: "Doc Resource Type", value: "ResourceType" },
    { label: "Doc Index", value: "DocIndex" },
    { label: "No of pages", value: "NoOfPageInDoc" },
    { label: "Doc Control ID", value: "DocControlID" },
    { label: "Latitude", value: "Latitude" },
    { label: "Longitude", value: "Longitude" }
  ];

  const [parameter_arr, setParameter_arr] = useState(parameter_arr_Default);
  const [associatedField, setAssociatedFieldKeys] = useState([]);
  const [savedParameter_json, setSavedParameter_json] = useState(
    props?.data?.mappingJson || {
      DocType: "",
      DocName: "",
      DocSize: "",
      DocComment: "",
      ResourceType: "",
      DocIndex: "",
      NoOfPageInDoc: "",
      DocControlID: "",
      Latitude: "",
      Longitude: ""
    }
  );

  function handleSavedParameterJson(key, value) {
    setSavedParameter_json((prev) => {
      return { ...prev, [key]: value };
    });
  }

  function handleClose() {
    props.handleOpen(false);
  }

  async function recursionMapping(contentData) {
    Object.values?.(contentData).map((content) => {
      if (content?.children && content["children"] && content["VariableName"] !== props.data.dataclass) {
        recursionMapping(content["children"]);
      }
      if (
        content["treeName"] === props.data.dataclass ||
        (content["children"] && content["VariableName"] === props.data.dataclass)
      ) {
        let associatedMap = new Map();
        let temp = [];
        temp.push({ VariableName: "Select" });
        Object.values(content["children"]).map((columnContent) => {
          associatedMap.set(columnContent["VariableName"], columnContent["VariableType"]);
          temp.push(columnContent);
        });

        setAdditionalFields(temp);
      }
    });
  }

  function setAdditionalFields(dataObject = []) {
    setAssociatedFieldKeys(dataObject);

    const additionalDO = [];

    if (dataObject.length) {
      dataObject.forEach((item) => {
        if (
          !parameter_arr_Default.some((param) => param?.value?.toLowerCase() === item?.VariableName?.toLowerCase()) &&
          item.VariableName !== "referencekey" &&
          item.VariableName !== "Select" &&
          item.VariableName !== "insertionorderid"
        ) {
          additionalDO.push({ label: item?.VariableName, value: item?.VariableName });
        }
      });
    }
    setParameter_arr((prev) => [...prev, ...additionalDO]);
  }

  useEffect(() => {
    recursionMapping(dataObjectsContent);
  }, []);

  function saveParamter_json() {
    props.saveParamter(savedParameter_json);
  }

  return (
    <>
      <div className={styles.shade_main}></div>

      <div className={styles.question_popup} style={{ opacity: 1 }}>
        <div className={styles.header}>
          <span> {t(`translation:${"DOCUMENT_PARAMETER_MAPPING"}`)} </span>
          <span onClick={handleClose} style={{ cursor: "pointer" }}>
            X
          </span>
          <hr width="100%" />
        </div>

        <div className={styles.body}>
          {parameter_arr.map((item) => {
            return (
              <>
                <div
                  style={{ display: "flex", justifyContent: "space-between", padding: "5px 0", alignItems: "center" }}
                >
                  <span> {t(`translation:${item.label}`)} </span>

                  <select
                    value={savedParameter_json[item.value]}
                    defaultValue={savedParameter_json[item.value]}
                    onChange={(e) => handleSavedParameterJson(item.value, e.target.value)}
                  >
                    {associatedField?.map((field) => {
                      return (
                        <option value={field.VariableName} key={field.VariableName}>
                          {field.VariableName === "Select"
                            ? t(`translation:${field.VariableName}`)
                            : field.VariableName}
                        </option>
                      );
                    })}
                  </select>
                </div>
              </>
            );
          })}
        </div>

        <div className={styles.footer}>
          <DullButton onClick={handleClose}>{t(`translation:${"Cancel"}`)}</DullButton>
          <BlueButton onClick={saveParamter_json}>{t(`translation:${"Save"}`)}</BlueButton>
        </div>
      </div>
    </>
  );
}

export default Doclist_parameter_mapping;
